@mixin telefono {
	@media (min-width: $telefono) {
		@content;
	}
}

@mixin tablet {
	@media (min-width: $tablet) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: $desktop) {
		@content;
	}
}

@mixin grid($columnas) {
	display: grid;
	grid-template-columns: repeat($columnas, 1fr);
}

@mixin display() {
	@content;
}
